export default class AppHeader extends HTMLElement {
    constructor() {
        super();
        this.innerHTML = AppHeader.template();
    }
    static template() {
        return `
    <section class="start">
      <span id="title">Pokmon Ierukana</span>
    </section>
    <section class="end">
      <icon-button id="setting" aria-label="" icon-name="settings"></icon-button>
    </section>`;
    }
}
